import { FC, useContext, useEffect, useRef, useState } from 'react';
import './styles.scss';
import axios from 'axios';
import { Context } from '../App';
// import Card from '../Card';
// import Button from '../Button';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface IPhoto {
  photo: boolean;
  setPhoto: React.Dispatch<React.SetStateAction<boolean>>;
}

const EdaFrame: FC<IPhoto> = ({ setPhoto }) => {
  const videoRef = useRef<HTMLVideoElement | null>(null);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const wordRef = useRef<HTMLDivElement | null>(null);

  const [name, setName] = useState({ tag: '', img: '', done: false });
  const { setValue, setFocus } = useContext(Context);

  useEffect(() => {
    console.log('mount');
    let stream: MediaStream | null;
    navigator.mediaDevices
      .getUserMedia({
        video: {
          facingMode: 'environment'
        },
        audio: false
      })
      .then((s) => {
        stream = s;
        if (videoRef.current != null) {
          videoRef.current.srcObject = stream;
        }
      });
    return () => {
      console.log('cleanUp');
      if (stream != null) {
        const tracks = stream.getTracks();
        tracks.forEach((track: { stop: () => void }) => {
          track.stop();
        });
      }
      if (videoRef.current != null) {
        stream = videoRef.current.srcObject as MediaStream;
        if (stream != null) {
          const tracks = stream.getTracks();
          tracks.forEach((track: { stop: () => void }) => {
            track.stop();
          });
        }
        videoRef.current.srcObject = null;
      }
    };
  }, []);

  const makePicture = async () => {
    const canvas = canvasRef.current;
    const video = videoRef.current;
    if (canvas != null && video != null) {
      canvas.width = video.width * 4;
      canvas.height = video.height * 4;
      const ctx = canvas.getContext('2d');
      if (ctx != null) {
        ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        const image_data_url = canvas.toDataURL('image/jpeg', 0.7);
        setName({ tag: 'Загрузка...', img: image_data_url, done: false });
        const url = 'https://imgtags.onrender.com/gettags';
        //const url = 'http://localhost:3001/gettags'

        const resp = await axios.post(url, { params: { img: image_data_url } });
        console.log(resp.data);
        if (resp.data == '') {
          setName({ tag: 'Попробуй ещё раз', img: image_data_url, done: false });
        } else {
          setName({ tag: resp.data, img: image_data_url, done: true });
        }
      }
    }
  };

  return (
    <div className='photo'>
      <div className='photo__body'>
        <video
          className='photo__video'
          playsInline
          muted
          ref={videoRef}
          width='100%'
          height='100%'
          autoPlay
        ></video>
      </div>
      <div className='photo__frame'>
        <div className='photo__target' />
      </div>
      <div className='photo__card'>
        <canvas className='photo__canvas' ref={canvasRef}></canvas>
        <div className='photo__word' ref={wordRef}>
          {name.tag}
        </div>
        {name.done && (
          <div
            className='photo__plus'
            onClick={() => {
              setValue(name.tag);
              setFocus(true);
              setPhoto(false);
            }}
          >
            +
          </div>
        )}
      </div>
      <div className='photo__button' onClick={makePicture}></div>
      {/* {name.tag !== '' && <Card tag={name.tag} image={name.img} />} */}
    </div>
  );
};

export default EdaFrame;
