import { FC } from 'react';
import { IResponseItem } from '../../types';
import './styles.scss';

const Suggestion: FC<IResponseItem> = ({ name, type, brand_id, place_name }) => {
  const currentUrl = window.location.href;
  return (
    <li
      className='suggestion'
      onClick={() => (window.location.href = `${currentUrl}?brandId=${brand_id}`)}
    >
      <div className={`suggestion__photo suggestion__photo_${type}`} />
      <div className='suggestion__info'>
        <h2 className='suggestion__name'>{name}</h2>
        <h3 className='suggestion__place'>
          {type === 'PLACE' ? 'Ресторан' : place_name[0].toUpperCase() + place_name.slice(1)}
        </h3>
      </div>
      <button className='suggestion__button'>Перейти</button>
    </li>
  );
};

export default Suggestion;
