import { ChangeEvent, useEffect, useRef, useState, useContext, FC } from 'react';
import { Textinput as DesktopInput } from '@yandex/ui/Textinput/desktop/bundle';
import { ISearchbar } from '../../types';
import { Button } from '@yandex/ui/Button/desktop/bundle';
import useKeyPress from '../../hooks/useKeyPress';
import { Context } from '../App';
import './styles.scss';
import IconRight from '../IconRight';

const Searchbar: FC<ISearchbar> = ({ value, setValue, hint, isDesktop, redirect }) => {
  const arrowRightPressed = useKeyPress('ArrowRight');
  const { focus, setFocus } = useContext(Context);
  const [hintOffset, setHintOffset] = useState<number>(0);
  const hintSampleRef = useRef<HTMLSpanElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  useEffect(() => {
    if (arrowRightPressed) {
      if (hint && isDesktop) {
        setValue(hint);
      }
    }
  }, [arrowRightPressed]);
  useEffect(() => {
    if (isDesktop) {
      hintSampleRef.current!.innerHTML = value;
      setHintOffset(hintSampleRef.current!.clientWidth);
    }
  }, [value]);
  useEffect(() => {
    if (!focus) {
      inputRef.current?.blur();
    } else {
      inputRef.current?.focus();
    }
  }, [focus]);
  useEffect(() => {
    inputRef.current?.addEventListener('keydown', (e) => {
      if (e.key == 'ArrowUp' || e.key == 'ArrowDown') e.preventDefault();
    });
    return () => {
      inputRef.current?.removeEventListener('keydown', (e) => {
        if (e.key == 'ArrowUp' || e.key == 'ArrowDown') e.preventDefault();
      });
    };
  }, []);
  return (
    <div className={focus ? 'searchbar searchbar_focused' : 'searchbar'}>
      <span className='searchbar__hint-sample' ref={hintSampleRef}></span>
      {isDesktop && focus && value && hint?.slice(0, value.length) === value && (
        <span
          className='searchbar__hint'
          style={{
            left: `calc(3rem + ${hint[value.length] === ' ' ? hintOffset + 4 : hintOffset}px)`
          }}
        >
          {hint?.slice(value?.length)}
        </span>
      )}
      {isDesktop && (
        <Button className='searchbar__search-button' onClick={() => redirect(value)}>
          Найти
        </Button>
      )}
      <DesktopInput
        className='searchbar__input'
        size='m'
        type='text'
        controlRef={inputRef}
        placeholder={isDesktop ? 'Найти ресторан, блюдо или товар' : 'Блюдо, ресторан или продукты'}
        hasClear
        iconRight={<IconRight value={value} />}
        value={value}
        onChange={(e: ChangeEvent<HTMLInputElement>) => setValue(e.target.value)}
        onFocus={() => setFocus(true)}
      />
    </div>
  );
};

export default Searchbar;
