import { Dispatch } from 'react';

export interface IResponseItem {
  itemReference: null;
  name: string;
  type: 'RESTAURANT_ITEM' | 'CATEGORY' | 'TEXT_SUGGEST' | 'PLACE' | 'GROCERY_ITEM';
  score: number;
  place_name: string;
  brand_id: number;
}

export interface ISearchbar {
  value: string;
  isDesktop: boolean;
  hint: string;
  setValue: Dispatch<React.SetStateAction<string>>;
  redirect: (value: string) => void;
}

export enum ActionTypes {
  ARROW_UP = 'arrowUp',
  ARROW_DOWN = 'arrowDown',
  SELECT = 'select',
  SET_ITEMS = 'setItems'
}

interface ArrowUpAction {
  type: ActionTypes.ARROW_UP;
}

interface ArrowDownAction {
  type: ActionTypes.ARROW_DOWN;
}

interface SelectAction {
  type: ActionTypes.SELECT;
  payload: number;
}

interface SetItemsAction {
  type: ActionTypes.SET_ITEMS;
  payload: IResponseItem[];
}

export type IAction = ArrowUpAction | ArrowDownAction | SelectAction | SetItemsAction;

export interface IPhoto {
  search: string;
}

export enum EThemeValue {
  light = 'light',
  dark = 'dark'
}

export interface IContext {
  focus: boolean;
  setFocus: Dispatch<React.SetStateAction<boolean>>;
  value: string;
  setValue: Dispatch<React.SetStateAction<string>>;
}

export interface IconRightI {
  value: string;
}

export interface IApiResponce {
  hints: IResponseItem[];
  suggestions: IResponseItem[];
  categories: IResponseItem[];
}

export interface IHint {
  text: string;
  index: number;
  selectedIndex: number;
  type: string;
  selectHint: (index: number) => void;
  onClickHandler: (item: string) => void;
  setApiResponce: Dispatch<React.SetStateAction<IApiResponce>>;
}

export interface IHintList {
  hints: IResponseItem[];
  setCurrentHint: Dispatch<React.SetStateAction<string>>;
  setValue: Dispatch<React.SetStateAction<string>>;
  isDesktop: boolean;
  redirect: (item: string) => void;
  suggestions: number;
  setApiResponce: Dispatch<React.SetStateAction<IApiResponce>>;
}

export interface IState {
  hints: IResponseItem[];
  selectedIndex: number;
}
