export const getHintsFromHistory = () => {
  const hints = localStorage.getItem('history');
  if (hints) return JSON.parse(hints).reverse();
  return [];
};

export const isInLocalStorage = (key: string, item: string): boolean => {
  const items = localStorage.getItem(key);
  if (items) return JSON.parse(items).includes(item);
  return false;
};

export const addHintInHistory = (hint: string): void => {
  const hints = getHintsFromHistory();
  if (!hints.includes(hint)) hints.push(hint);
  localStorage.setItem('history', JSON.stringify(hints));
};

export const removeHintFromHistory = (hint: string): void => {
  const hints = getHintsFromHistory();
  const newHints = hints.filter((__hint: string) => __hint !== hint);
  localStorage.setItem('history', JSON.stringify(newHints));
};
