import { FC } from 'react';
import { IResponseItem } from '../../types';
import Suggestion from '../Suggestion';
import './styles.scss';

interface ISuggestionsList {
  suggestions: IResponseItem[];
  hints: number;
}

const SuggestionsList: FC<ISuggestionsList> = ({ suggestions, hints }) => {
  return (
    <ul
      className={
        suggestions.length
          ? hints
            ? 'suggestionsList'
            : 'suggestionsList suggestionsList_only'
          : 'suggestionsList suggestionsList_hidden'
      }
    >
      {suggestions.map((suggestion, index) => (
        <Suggestion key={index} {...suggestion} />
      ))}
    </ul>
  );
};

export default SuggestionsList;
