import { useEffect } from 'react';

export const useEvent = (event: string, handler: (ev: Event) => void, passive = false) => {
  useEffect(() => {
    let resizeTimeout: NodeJS.Timeout | null;

    function resizeThrottler(e: Event) {
      if (!resizeTimeout) {
        resizeTimeout = setTimeout(() => {
          resizeTimeout = null;
          handler(e);
        }, 66);
      }
    }

    window.addEventListener(event, resizeThrottler, passive);

    return () => {
      window.removeEventListener(event, resizeThrottler);
    };
  });
};
