import { FC, useState, MouseEvent, useContext } from 'react';
import { Context } from '../App';
import { IHint } from '../../types';
import { removeHintFromHistory, isInLocalStorage } from '../../localStorage';
import { getInitialApiResponce } from '../Search';
import './styles.scss';

const Hint: FC<IHint> = ({
  text,
  index,
  selectedIndex,
  type,
  selectHint,
  onClickHandler,
  setApiResponce
}) => {
  const [inHistory, setInHistory] = useState<boolean>(isInLocalStorage('history', text));
  const { value } = useContext(Context);
  const deleteHint = (e: MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    if (inHistory) removeHintFromHistory(text);
    if (!value) setApiResponce(getInitialApiResponce());
    else setInHistory(false);
  };
  return (
    <li
      className={selectedIndex === index ? 'hint hint_selected' : 'hint'}
      onMouseOver={() => selectHint(index)}
      onClick={() => onClickHandler(text)}
    >
      {inHistory && <div className='hint__history' />}
      {text.match(new RegExp(value, 'i')) ? (
        <h4 className={type === 'CATEGORY' ? 'hint__text hint__text_category' : 'hint__text'}>
          {text.slice(0, text.toLowerCase().indexOf(value))}
          <span>
            {text.toLocaleLowerCase().indexOf(value) === 0 && type === 'CATEGORY' && value
              ? value[0].toUpperCase() + value.slice(1)
              : value}
          </span>
          {text.slice(text.toLowerCase().indexOf(value) + value.length)}
        </h4>
      ) : (
        <h4 className={type === 'CATEGORY' ? 'hint__text hint__text_category' : 'hint__text'}>
          {text}
        </h4>
      )}
      <button
        className={inHistory ? 'hint__icon hint__icon_delete' : 'hint__icon hint__icon_link'}
        onClick={deleteHint}
      />
    </li>
  );
};

export default Hint;
