import './styles.scss';
import React, { FC, useCallback, useContext, useEffect } from 'react';
import { Context } from '../App';

interface Audio {
  audio: boolean;
  setAudio: React.Dispatch<React.SetStateAction<boolean>>;
}

const Audio: FC<Audio> = ({ audio, setAudio }) => {
  const { setValue, setFocus } = useContext(Context);
  let query = '';

  const closePopup = useCallback((e: MouseEvent) => {
    if ((e.target as HTMLDivElement).classList.contains('audio-wrapper')) {
      setAudio(false);
    }
  }, []);

  useEffect(() => {
    if (audio) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const speechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      const recognition = new speechRecognition();

      recognition.continuous = true;
      recognition.maxAlternatives = 3;
      recognition.lang = 'ru-RU';

      recognition.onerror = ({ error }: { error: string }): void => {
        console.error(error);
      };

      recognition.onresult = (e: { results: { transcript: string }[][] }) => {
        query = e.results[0][0].transcript;
        setValue(query);
        setFocus(true);
        recognition.stop();
        setAudio(false);
      };
      const audioWrapper = document.querySelector('.audio-wrapper') as HTMLDivElement;
      recognition.start();
      audioWrapper.addEventListener('mousedown', closePopup);
      return () => {
        audioWrapper.removeEventListener('mousedown', closePopup);
      };
    }
  }, []);

  return (
    <div className='audio-wrapper'>
      <div className='audio'>
        <div className='audio__title'>
          <span className='audio__char' style={{ animationDelay: '0s' }}>
            Г
          </span>
          <span className='audio__char' style={{ animationDelay: '0.02s' }}>
            о
          </span>
          <span className='audio__char' style={{ animationDelay: '0.05s' }}>
            в
          </span>
          <span className='audio__char' style={{ animationDelay: '0.08s' }}>
            о
          </span>
          <span className='audio__char' style={{ animationDelay: '0.1s' }}>
            р
          </span>
          <span className='audio__char' style={{ animationDelay: '0.13s' }}>
            и
          </span>
          <span className='audio__char' style={{ animationDelay: '0.16s' }}>
            т
          </span>
          <span className='audio__char' style={{ animationDelay: '0.19s' }}>
            е
          </span>
          <span className='audio__char' style={{ animationDelay: '0.21s' }}>
            .
          </span>
          <span className='audio__char' style={{ animationDelay: '0.24s' }}>
            .
          </span>
          <span className='audio__char' style={{ animationDelay: '0.27s' }}>
            .
          </span>
        </div>
      </div>
    </div>
  );
};

export default Audio;
