import { useEffect, useState, useContext } from 'react';
import useWindowDimensions from '../../hooks/useWindowDimensions';
import { api } from '../../api/api';
import { useDebounce } from '../../hooks/useDebounce';
import { IApiResponce } from '../../types';
import Searchbar from '../Searchbar';
import { Context } from '../App';
import HintList from '../HintList';
import SuggestionsList from '../SuggestionsList';
import './styles.scss';
import { getHintsFromHistory, addHintInHistory } from '../../localStorage';

export const getInitialApiResponce = (): IApiResponce => ({
  hints: getHintsFromHistory().map((hint: string) => ({
    itemReference: null,
    name: hint,
    type: 'TEXT_SUGGEST',
    score: 0
  })),
  suggestions: [],
  categories: []
});

const Search = () => {
  const { innerWidth } = useWindowDimensions();
  const [apiResponce, setApiResponce] = useState<IApiResponce>(getInitialApiResponce());
  const { value, setValue, focus, setFocus } = useContext(Context);
  const [isDesktop, setIsDesktop] = useState<boolean>(innerWidth > 768);
  const [currentHint, setCurrentHint] = useState<string>('');
  const debouncedValue = useDebounce(value, 50);
  const redirect = (item: string): void => {
    window.open(`https://eda.yandex.ru/search?query=${item}&type=all`);
    addHintInHistory(item);
    setFocus(false);
  };

  const getBrandId = (string: string) => {
    if (!string) {
      return undefined;
    }
    const startIndex = string.indexOf('=') + 1;
    return +string.slice(startIndex);
  };

  useEffect(() => {
    if (!debouncedValue) setApiResponce(getInitialApiResponce());
    else {
      const brandId = getBrandId(window.location.search);
      if (brandId) {
        api(debouncedValue, brandId).then(({ items, queries, categories }) => {
          setApiResponce({
            hints: queries,
            suggestions: items,
            categories
          });
        });
      } else {
        api(debouncedValue).then(({ items, places, queries, categories }) => {
          {
            setApiResponce({
              hints: [...queries.slice(0, 4), ...categories.slice(0, 2)],
              suggestions: [...items, ...places],
              categories
            });
          }
        });
      }
    }
  }, [debouncedValue]);

  useEffect(() => {
    if (innerWidth > 768) setIsDesktop(true);
    else setIsDesktop(false);
  }, [innerWidth]);

  return (
    <div
      className={focus ? 'search search_focused' : 'search'}
      onClick={(e) => e.stopPropagation()}
    >
      <Searchbar
        value={value}
        setValue={setValue}
        hint={currentHint}
        isDesktop={isDesktop}
        redirect={redirect}
      />
      {focus && (
        <div className='search__content'>
          <HintList
            hints={apiResponce.hints}
            setCurrentHint={setCurrentHint}
            setValue={setValue}
            isDesktop={isDesktop}
            redirect={redirect}
            suggestions={apiResponce.suggestions.length}
            setApiResponce={setApiResponce}
          />
          <SuggestionsList
            hints={apiResponce.hints.length}
            suggestions={apiResponce.suggestions.slice(0, 4)}
          />
        </div>
      )}
    </div>
  );
};

export default Search;
