import { useEffect, useState } from 'react';
import './styles.scss';
import { useEvent } from '../../hooks/useEvent';
import { darkStyles, themeValue } from '../../constants';
import { EThemeValue } from '../../types';

function ThemeSwitch() {
  const [theme, setTheme] = useState<EThemeValue>(
    (localStorage.getItem(themeValue) as EThemeValue) || EThemeValue.light
  );
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);

  const onChange = () => {
    setTheme((prevState) =>
      prevState === EThemeValue.light ? EThemeValue.dark : EThemeValue.light
    );
  };

  useEvent('resize', (e) => setScreenWidth((e.target as Window).innerWidth));

  useEffect(() => {
    localStorage.setItem(themeValue, theme);
  }, [theme]);

  return (
    <>
      {screenWidth > 768 && (
        <>
          <svg
            className={theme === EThemeValue.light ? `fill_light` : `fill_dark`}
            onClick={() => onChange()}
            width='42'
            height='42'
            viewBox='-9 -8 42 42'
            xmlns='http://www.w3.org/2000/svg'
          >
            <path
              className={theme === EThemeValue.dark ? `v_dark` : 'h_dark'}
              d='M15.1.4a1 1 0 0 0-1.22.72l-.65 2.41a1 1 0 1 0 1.94.52l.64-2.42a1 1 0 0 0-.7-1.22zM12 18.5a6.5 6.5 0 1 1 0-13 6.5 6.5 0 0 1 0 13zm-3.17 1.45a1 1 0 1 1 1.94.52l-.65 2.41a1 1 0 0 1-1.93-.51l.64-2.42zm-4.69-4.57a1 1 0 1 1 1 1.74l-2.17 1.25a1 1 0 1 1-1-1.74l2.17-1.25zM22.39 6a1 1 0 0 0-1.36-.37l-2.17 1.25a1 1 0 1 0 1 1.74l2.17-1.25A1 1 0 0 0 22.39 6zM4.05 8.83a1 1 0 0 1-.52 1.94l-2.41-.65a1 1 0 1 1 .51-1.93l2.42.64zm19.54 6.28a1 1 0 0 0-.7-1.23l-2.42-.65a1 1 0 0 0-.52 1.94l2.42.64a1 1 0 0 0 1.22-.7zM8.62 4.14a1 1 0 1 1-1.74 1L5.63 2.97a1 1 0 1 1 1.74-1l1.25 2.17zM18 22.39a1 1 0 0 0 .37-1.36l-1.25-2.17a1 1 0 0 0-1.74 1l1.25 2.17a1 1 0 0 0 1.37.36z'
            ></path>{' '}
            <path
              className={theme === EThemeValue.light ? `v_light` : `h_light`}
              d='M14.59 21.66A11.04 11.04 0 0 1 3.6 18.03a1 1 0 0 1 .75-1.63c3.98-.11 6.66-1.48 8.17-4.1 1.51-2.6 1.35-5.61-.54-9.12a1 1 0 0 1 1.04-1.46c3.9.59 7.6 3.82 8.63 7.7a10 10 0 0 1-7.07 12.24Z'
            ></path>
          </svg>
          {/* <input
            type='checkbox'
            id='toggle'
            className='toggle--checkbox'
            onClick={onChange}
            defaultChecked={theme === EThemeValue.dark}
          />
          <label htmlFor='toggle' className='toggle--label'>
            <span className='toggle--label-background' />
          </label> */}
        </>
      )}
      <style media={theme === EThemeValue.light ? 'none' : 'screen'}>{darkStyles}</style>
    </>
  );
}

export default ThemeSwitch;
