import { FC } from 'react';
import './styles.scss';

export interface IRestaurant {
  index?: number;
  name: string;
  rating: number;
  link: string;
  badge: string;
}

const Restaurant: FC<IRestaurant> = ({ index, name, rating, link, badge }) => {
  const ratings = {
    4.7: 'Хорошо',
    4.8: 'Рекомендуем',
    4.9: 'Отлично'
  };
  const icons = {
    4.7: 'good',
    4.8: 'recommend',
    4.9: 'excellent'
  };
  const badges = {
    'Бесплатная доставка': 'delivery',
    'Скидка 25%': 'discount',
    'Блюдо в подарок': 'gift'
  };
  return (
    <a className='restaurant' href={link} target='_blank' rel='noreferrer'>
      <div className={`restaurant__photo restaurant__photo_${index}`}>
        <div className='restaurant__badge'>
          <div
            className={`restaurant__badge-icon restaurant__badge-icon_${
              badges[badge as keyof typeof badges]
            }`}
          />
          <div className='restaurant__badge-text'>{badge}</div>
        </div>
      </div>
      <h4 className='restaurant__name'>{name}</h4>
      <div className='restaurant__info'>
        <div
          className={`restaurant__rating-icon restaurant__rating-icon_${
            icons[rating as keyof typeof ratings]
          }`}
        />
        <span className='restaurant__rating'>{`${rating} ${
          ratings[rating as keyof typeof ratings]
        } ₽ ₽ ₽`}</span>
        <span className=''></span>
      </div>
    </a>
  );
};

export default Restaurant;
