import { FC, useEffect, useReducer } from 'react';
import useKeyPress from '../../hooks/useKeyPress';
import { ActionTypes, IAction, IHintList, IState } from '../../types';
import Hint from '../Hint';
import { isInLocalStorage } from '../../localStorage';
import './styles.scss';

const initialState: IState = {
  hints: [],
  selectedIndex: 0
};

const reducer = (state: IState = initialState, action: IAction): IState => {
  switch (action.type) {
    case ActionTypes.ARROW_UP:
      return {
        ...state,
        selectedIndex: state.selectedIndex !== 0 ? state.selectedIndex - 1 : state.hints.length - 1
      };
    case ActionTypes.ARROW_DOWN:
      return {
        ...state,
        selectedIndex: state.selectedIndex !== state.hints.length - 1 ? state.selectedIndex + 1 : 0
      };
    case ActionTypes.SELECT:
      return {
        ...state,
        selectedIndex: action.payload as number
      };
    case ActionTypes.SET_ITEMS:
      return { ...state, hints: action.payload };
    default:
      return { ...state };
  }
};

const HintList: FC<IHintList> = ({
  hints,
  setCurrentHint,
  setValue,
  isDesktop,
  redirect,
  suggestions,
  setApiResponce
}) => {
  const arrowUpPressed = useKeyPress('ArrowUp');
  const arrowDownPressed = useKeyPress('ArrowDown');
  const enterPressed = useKeyPress('Enter');
  const [state, dispatch] = useReducer(reducer, initialState);
  useEffect(() => {
    if (arrowUpPressed) {
      dispatch({ type: ActionTypes.ARROW_UP });
    }
    if (arrowDownPressed) {
      dispatch({ type: ActionTypes.ARROW_DOWN });
    }
    if (enterPressed) {
      redirect(state.hints[state.selectedIndex].name);
    }
  }, [arrowUpPressed, arrowDownPressed, enterPressed]);
  useEffect(() => {
    hints.sort(
      (h1, h2) => +isInLocalStorage('history', h2.name) - +isInLocalStorage('history', h1.name)
    );
    dispatch({
      type: ActionTypes.SET_ITEMS,
      payload: hints.length ? hints : []
    });
    setCurrentHint(hints[state.selectedIndex]?.name);
  }, [hints, state.selectedIndex]);
  const selectHint = (index: number) => {
    dispatch({ type: ActionTypes.SELECT, payload: index });
  };
  const onClickHandler = (text: string): void => {
    if (isDesktop) {
      redirect(text);
    } else setValue(text);
  };
  return (
    <ul
      className={
        state.hints.length
          ? suggestions
            ? 'hintList'
            : 'hintList hintList_only'
          : 'hintList hintList_hidden'
      }
    >
      {state.hints.map((hint, index) => (
        <Hint
          key={hint.name}
          text={hint.name}
          index={index}
          type={hint.type}
          selectedIndex={state.selectedIndex}
          selectHint={selectHint}
          onClickHandler={onClickHandler}
          setApiResponce={setApiResponce}
        />
      ))}
    </ul>
  );
};

export default HintList;
